.error-page.error {
    color: #000;
    background: #fff;
    font-family: -apple-system, BlinkMacSystemFont, Roboto, "Segoe UI",
    "Fira Sans", Avenir, "Helvetica Neue", "Lucida Grande", sans-serif;
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.error-page .desc {
    display: inline-block;
    text-align: left;
    line-height: 49px;
    height: 49px;
    vertical-align: middle;
    margin-bottom: 20px;
}

.error-page h1 {
    display: inline-block;
    border-right: 1px solid rgba(0, 0, 0, .3);
    margin: 0 20px 0 0;
    padding: 10px 23px 10px 0;
    font-size: 24px;
    font-weight: 500;
    vertical-align: top;
}

.error-page h2 {
    font-size: 14px;
    font-weight: normal;
    margin: 0;
    padding: 0;
}